import { Injectable } from "@angular/core";
import { LoggerServiceProvider } from "src/providers/logger-service/logger-service";
import { ConfigProvider } from "src/providers/utils/config";
import { UtilService } from "src/providers/utils/utils.service";

declare var WLResourceRequest;
/*
  Generated class for the PaymentHistoryService .

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class PaymentHistoryService {

  constructor(private utils: UtilService, private logger: LoggerServiceProvider) {
    this.logger.log("Get Payment History service");
  }

   /** Get user policies **/
   getPaymentHistory(isEmandate){
    this.logger.trace("PaymentHistoryService--getPolicyList--start");
    let promise = new Promise((resolve, reject) => {
      try {
        if (this.utils.isDeviceOnLine()) {
            var resourceRequest;
            if(isEmandate){
              resourceRequest = new WLResourceRequest(ConfigProvider.getEMandatePaymentHistory, WLResourceRequest.GET);
            } else {
              resourceRequest = new WLResourceRequest(ConfigProvider.PaymentHistoryAdapterPath, WLResourceRequest.GET);
            }

            resourceRequest.setQueryParameter('params',"['"+this.utils.getEncryptedUserId()+"','"+this.utils.getToken()+"']");
            resourceRequest.send().then((response) => {
              resolve(response);
            },
              (error) => {
                this.logger.error("PaymentHistoryService--getPolicyList Adapter calling error:" + JSON.stringify(error));
                reject(error);
            });
        }
      }catch (e) {
        reject(e);
        this.logger.log("PaymentHistoryService--getPolicyList adapter calling catch error:" + JSON.stringify(e));
      }
    });
    this.logger.trace("PaymentHistoryService--getPolicyList--end");
    return promise;
   }
}
