import { Injectable } from "@angular/core";
import { LoggerServiceProvider } from "src/providers/logger-service/logger-service";
import { ConfigProvider } from "src/providers/utils/config";
import { UtilService } from "src/providers/utils/utils.service";

declare var WLResourceRequest;
/*
  Generated class for the FundAllocationsService .

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class HealthinfoService {


  constructor( public logger: LoggerServiceProvider, public utils : UtilService) {
    this.logger.log("HealthinfoService");
  }

   /** Get Photo Gallery **/
   getHealthInfo(pageNo) {
    this.logger.trace("HealthinfoService--getHealthInfo--start " );

    let promise = new Promise((resolve, reject) => {
      try {
        var resourceRequest =  new WLResourceRequest(ConfigProvider.getHealthInfoAdapterPath, WLResourceRequest.GET);
        resourceRequest.setQueryParameter('params',[this.utils.getToken(),pageNo]);
        resourceRequest.send().then((response) => {
          resolve(response);
        },
        (error) => {
          this.logger.error("HealthinfoService-- Adapter calling error:" + JSON.stringify(error));
          reject(error);
        });
      }catch (e) {
        this.logger.log("HealthinfoService calling catch error:" + JSON.stringify(e));
        reject(e);
      }
    });
    this.logger.trace("HealthinfoService--getHealthInfo--end");
    return promise;
   }
};



