
import { Injectable } from '@angular/core';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { UtilService } from 'src/providers/utils/utils.service';
import { ConfigProvider } from 'src/providers/utils/config';
declare var WLResourceRequest;
//declare var self;
/*
  Generated class for the EPolicyListService.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class EPolicyListService {
  

  constructor( public logger: LoggerServiceProvider ,public utils : UtilService, private config: ConfigProvider) {
    this.logger.log("EPolicyListService");
    this.config.currentPage.pageName = "EPolicyListService";
  }
   
   /** Get EPolicyList **/
   getEPolicyList() {
    this.logger.trace("EPolicyListService--getEPolicyList--start " );

    let promise = new Promise((resolve, reject) => {
      try {
        var resourceRequest =  new WLResourceRequest(ConfigProvider.getEPolicyList, WLResourceRequest.GET);          
        resourceRequest.setQueryParameter('params',[this.utils.getUserIdentity(),this.utils.getToken()]);   
        resourceRequest.send().then((response) => {
          resolve(response);
        },
        (error) => {
          this.logger.error("EPolicyListService-- Adapter calling error:" + JSON.stringify(error));
          reject(error);
        });
      }catch (e) {
        this.logger.log("EPolicyListService calling catch error:" + JSON.stringify(e));
        reject(e);
      }
    });
    this.logger.trace("EPolicyListService--getEPolicyList--end");
    return promise;
   }
}
