import { Injectable } from "@angular/core";
import { LoggerServiceProvider } from "src/providers/logger-service/logger-service";
import { ConfigProvider } from "src/providers/utils/config";
import { UtilService } from "src/providers/utils/utils.service";


declare var WLResourceRequest;
//declare var self;
/*
  Generated class for the eNominationService.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class eNominationService {


  constructor( public logger: LoggerServiceProvider ,public utils : UtilService, private config: ConfigProvider) {
    this.logger.log("eNominationService");
    this.config.currentPage.pageName = "eNominationService";
  }
     /** Get getEnominationlink **/
   getEnominationPath() {
    this.logger.trace("eNominationService--getEnominationlink--start " );

    let promise = new Promise((resolve, reject) => {
      try {
        var resourceRequest =  new WLResourceRequest(ConfigProvider.getEnominationlinkAdapterPath, WLResourceRequest.GET);
        resourceRequest.setQueryParameter('params',[this.utils.getUserIdentity(),this.utils.getToken()]);
        resourceRequest.send().then((response) => {
          resolve(response);
        },
        (error) => {
          this.logger.error("eNominationService-- Adapter calling error:" + JSON.stringify(error));
          reject(error);
        });
      }catch (e) {
        this.logger.log("eNominationService calling catch error:" + JSON.stringify(e));
        reject(e);
      }
    });
    this.logger.trace("eNominationService--getEnominationlink--end");
    return promise;
   }
}
