
import { Injectable } from '@angular/core';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { UtilService } from 'src/providers/utils/utils.service';
import { ConfigProvider } from 'src/providers/utils/config';
declare var WLResourceRequest;
/*
  Generated class for the NewsLetterService.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class NewsLetterService {

  constructor(public logger: LoggerServiceProvider, public utils: UtilService) {
    this.logger.log("NewsLetterService");
  }

  /** Get NewsLettersList **/
  getNewsLettersList(pageNo) {
    this.logger.trace("NewsLetterService--getEPolicyList--start ");

    const promise = new Promise((resolve, reject) => {
      try {
        var resourceRequest = new WLResourceRequest(ConfigProvider.getDownloadList, WLResourceRequest.GET);
        resourceRequest.setQueryParameter('params', [this.utils.getToken(), pageNo]);
        resourceRequest.send().then((response) => {
          resolve(response);
        },
          (error) => {
            this.logger.error("NewsLetterService-- Adapter calling error:" + JSON.stringify(error));
            reject(error);
          });
      } catch (e) {
        this.logger.log("NewsLetterService calling catch error:" + JSON.stringify(e));
        reject(e);
      }
    });
    this.logger.trace("NewsLetterService--getNewsLettersList--end");
    return promise;
  }
}
