
import { Injectable } from '@angular/core';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';

declare var WLResourceRequest:any;//declare var self;
/*
  Generated class for the UnitPriceListService.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class MediaReleaseService {

  constructor( public config: ConfigProvider,public utils: UtilService) {
  }

  getMediaRelease(pageNo) {

    let promise = new Promise((resolve, reject) => {
      try {
        var resourceRequest =  new WLResourceRequest(ConfigProvider.getMediaRelease, WLResourceRequest.GET);

        resourceRequest.setQueryParameter('params',[pageNo]);

        resourceRequest.send().then((response) => {
          resolve(response);
        },
        (error) => {
          reject(error);
        });
      }catch (e) {
        reject(e);
      }
    });
    return promise;
   }
   getProfileDetails() {
    //alert('Profile Details')
    let promise = new Promise((resolve, reject) => {
        try {
            var resourceRequest = new WLResourceRequest(ConfigProvider.getProfileDetails, WLResourceRequest.GET);
            resourceRequest.setQueryParameter('params', [this.config.userInfo.encryptedUserId, this.config.userInfo.token]);
            resourceRequest.send().then((response) => {
              alert('Success')
                resolve(response);
            },
                (error) => {
                  alert('Error')
                   reject(error);
                });
        } catch (e) {
          alert('Error')
            reject(e);
        }
    });
    return promise;
}


getAccounHolderInfo(){
  alert('getAccounHolderInfo Details')

  let promise = new Promise((resolve, reject) => {
       try {
          let encryptedUserId = this.config.userInfo.encryptedUserId;
          let token = this.config.userInfo.token;

          let params = [encryptedUserId, token];
          var resourceRequest = new WLResourceRequest(ConfigProvider.getAccountHolderDetails, WLResourceRequest.POST);
          var data = {
            "params": JSON.stringify(params)
          };
          resourceRequest.sendFormParameters(data).then((response) => {
            alert('Success getAccounHolderInfo')
            resolve(response);
          },
            (error) => {
              alert('error getAccounHolderInfo')

              reject(error);
          });
    }catch (e) {
      reject(e);
    }
  });
  return promise;
 }
}


