import { Injectable } from '@angular/core';
import { UtilService } from 'src/providers/utils/utils.service';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';

declare var WLResourceRequest;

@Injectable()
export class CheckUserExistenceService {
  constructor(public utils: UtilService, private logger: LoggerServiceProvider){

  }

  CheckUserExistence(userId) {
    this.logger.trace("CheckUserExistenceService--CheckUserExistence--start userID " + userId );
    let promise = new Promise((resolve, reject) => {
      try {
          let params = [userId];
          var resourceRequest = new WLResourceRequest(ConfigProvider.CheckUserExistence, WLResourceRequest.POST);
          var data = {
            "params": JSON.stringify(params)
          };
          resourceRequest.sendFormParameters(data).then((response) => {
            resolve(response);
          },
            (error) => {
              this.logger.error("CheckUserExistenceService--CheckUserExistence Adapter calling error:" + JSON.stringify(error));
              reject(error);
            });
      }catch (e) {
        this.logger.log("CheckUserExistenceService--CheckUserExistence calling catch error:" + JSON.stringify(e));
        reject(e);
      }
    });
    this.logger.trace("CheckUserExistenceService--CheckUserExistence--end");
    return promise;
   }
}
