import { Injectable } from '@angular/core';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';

declare var WLResourceRequest:any;
/*
  Generated class for the PaymentRecordsService .

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class PaymentRecordsService {

  constructor(private utils: UtilService, private logger: LoggerServiceProvider) {
    this.logger.log("Get Payment Records service");
  }

   /** Get user policies **/
   getPaymentRecords(encryptedPolicyNo){
    this.logger.trace("PaymentRecordsService--getPaymentRecords--start");
    let promise = new Promise((resolve, reject) => {
      try {

            var resourceRequest = new WLResourceRequest(ConfigProvider.getPaymentRecords, WLResourceRequest.GET);
            resourceRequest.setQueryParameter('params',"['"+encryptedPolicyNo+"','"+this.utils.getToken()+"']");
            resourceRequest.send().then((response) => {
              resolve(response);
            },
              (error) => {
                this.logger.error("PaymentRecordsService--getPaymentRecords Adapter calling error:" + JSON.stringify(error));
                reject(error);
            });
      }catch (e) {
        reject(e);
        this.logger.log("PaymentRecordsService--getPaymentRecords adapter calling catch error:" + JSON.stringify(e));
      }
    });
    this.logger.trace("PaymentRecordsService--getPaymentRecords--end");
    return promise;
   }
}
