    /**this method is handle to servcie error response , commonly used for all service calls */
    handleServiceException(err: { [x: string]: { [x: string]: any; }; errorCode: any; errorMsg: any; }) {
        let errorMsg;
        if (!err['responseJSON']) {
            errorMsg = this.translate.instant('generic_msgs.server_err');
        } else if (
            err['responseJSON'] &&
            err['responseJSON']['statusCode'] == HttpStatus.BAD_REQUEST
        ) {
            let srvrResp = err['responseJSON']['errors'];
            try {
                if (JSON.parse(srvrResp[0]).error_description)
                    errorMsg = JSON.parse(srvrResp[0]).error_description;
                else if (JSON.parse(srvrResp[0]).userMessage)
                    errorMsg = JSON.parse(srvrResp[0]).userMessage;
                else if (JSON.parse(srvrResp[0]).Message)
                    errorMsg = JSON.parse(srvrResp[0]).Message;
                else errorMsg = this.translate.instant('generic_msgs.server_err');
            } catch (e) {
                errorMsg = srvrResp[0];
            }
        } else if (
            err['responseJSON'] &&
            err['responseJSON']['statusCode'] == HttpStatus.UNAUTHORIZED
        ) {
            errorMsg = err['responseJSON']['errors'][0].Message;
            this.publishSomeData({ 'session': 'expired'});
            // this.events.publish('session:expired');
        } else if (
            err['responseJSON'] &&
            (err['responseJSON']['statusCode'] == HttpStatus.INTERNAL_SERVER_ERROR ||
                err['responseJSON']['statusCode'] == HttpStatus.FORBIDDEN ||
                err['responseJSON']['statusCode'] == HttpStatus.CONFLICT ||
                err['responseJSON']['statusCode'] == HttpStatus.NOT_FOUND ||
                err['responseJSON']['statusCode'] == HttpStatus.NOT_ACCEPTABLE)
        ) {
            let srvrResp = err['responseJSON']['errors'];
            try {
                if (JSON.parse(srvrResp[0]).userMessage)
                    errorMsg = JSON.parse(srvrResp[0]).userMessage;
                else if (JSON.parse(srvrResp[0]).Message)
                    errorMsg = JSON.parse(srvrResp[0]).Message;
                else errorMsg = this.translate.instant('generic_msgs.server_err');
            } catch (e) {
                errorMsg = srvrResp[0];
            }
        } else if ((err.errorCode = 'REQUEST_TIMEOUT')) {
            errorMsg = err.errorMsg;
        } else {
            errorMsg = this.translate.instant('generic_msgs.server_err');
        }
        return errorMsg;
    }

