import { Injectable } from "@angular/core";
import { LoggerServiceProvider } from "src/providers/logger-service/logger-service";
import { ConfigProvider } from "src/providers/utils/config";
import { UtilService } from "src/providers/utils/utils.service";

declare var WLResourceRequest;

@Injectable()
export class SecurityQuestionsService {

  //retryCount: number = 0;

  constructor(public utils: UtilService, public logger: LoggerServiceProvider) {
    this.logger.log("Get Security Questions service");
  }

   /** post registration check **/
   getSecurityQuestions(){
    this.logger.trace("SecurityQuestionsService--getSecurityQuestions--start");
    let promise = new Promise((resolve, reject) => {
      try {
            var resourceRequest = new WLResourceRequest(ConfigProvider.getSecurityQuestions, WLResourceRequest.GET);
            resourceRequest.send().then((response) => {
              resolve(response);
            },
              (error) => {
                this.logger.error("SecurityQuestionsService--getSecurityQuestions  Adapter calling error:" + JSON.stringify(error));
                reject(error);
            });
      }catch (e) {
        reject(e);
        this.logger.log("SecurityQuestionsService--getSecurityQuestions calling catch error:" + JSON.stringify(e));
      }
    });
    this.logger.trace("SecurityQuestionsService--getSecurityQuestions--end");
    return promise;
   }


      /** post registration check **/
   getSocialData(){
    this.logger.trace("SecurityQuestionsService--getSecurityQuestions--start");
    let promise = new Promise((resolve, reject) => {
      try {
            var resourceRequest = new WLResourceRequest(ConfigProvider.getSocialPlatfromList, WLResourceRequest.GET);
            resourceRequest.send().then((response) => {
              resolve(response);
            },
              (error) => {
                this.logger.error("SecurityQuestionsService--getSecurityQuestions  Adapter calling error:" + JSON.stringify(error));
                reject(error);
            });
      }catch (e) {
        reject(e);
        this.logger.log("SecurityQuestionsService--getSecurityQuestions calling catch error:" + JSON.stringify(e));
      }
    });
    this.logger.trace("SecurityQuestionsService--getSecurityQuestions--end");
    return promise;
   }
}
