import { Injectable } from '@angular/core';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { UtilService } from 'src/providers/utils/utils.service';
import { ConfigProvider } from 'src/providers/utils/config';
declare var WLResourceRequest;
/*
  Generated class for the GetAppsListService.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class GetAppsListService {
  
  constructor(public utils: UtilService, public logger: LoggerServiceProvider,public config : ConfigProvider) {
    this.logger.log("GetAppsListService");
  }

   /** Get AppsList**/
   getApplist(){
    this.logger.trace("GetAppsListService--getApplist--start");
    let promise = new Promise((resolve, reject) => {
      try {
            var resourceRequest = new WLResourceRequest(ConfigProvider.getAppsList, WLResourceRequest.GET);
            resourceRequest.setQueryParameter('params',[this.utils.getToken()]);   
            resourceRequest.send().then((response) => {
              resolve(response);
            },
              (error) => {
                this.logger.error("GetAppsListService--getApplist Adapter calling error:" + JSON.stringify(error));
                reject(error);
            });
      }catch (e) {
        reject(e);
        this.logger.log("GetAppsListService--getApplist adapter calling catch error:" + JSON.stringify(e));
      }
    });
    this.logger.trace("GetAppsListService--getApplist--end");
    return promise;
   }
}