
import { Injectable } from '@angular/core';
declare var WLResourceRequest:any;//declare var self;
/*
  Generated class for the UnitPriceListService.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class UnitPriceListService {

  constructor( ) {
  }

   /** Unit Price LIst **/
   getUnitPriceList() {
    let promise = new Promise((resolve, reject) => {
      try {
        var resourceRequest =  new WLResourceRequest("/adapters/UnitPrice/GetUnitPriceList", WLResourceRequest.GET);
        console.log(resourceRequest);
        resourceRequest.send().then((response:any) => {
          resolve(response);
        },
        (error:any) => {
          reject(error);
        });
      }catch (e) {
        reject(e);
      }
    });
    return promise;
   }
}

