import { Component, OnInit } from '@angular/core';
import { ModalController, NavParams } from '@ionic/angular';

@Component({
  selector: 'app-success-popup',
  templateUrl: './success-popup.page.html',
  styleUrls: ['./success-popup.page.scss'],
})
export class SuccessPopupPage implements OnInit {

  redirectionMsg: string;
  title: string;
  extraSecondLineForReinstatement: any;

  constructor( public navParams: NavParams, public modalController: ModalController) {
    this.redirectionMsg = this.navParams.get('redirectionMsg');
    this.title = this.navParams.get('title');
    this.extraSecondLineForReinstatement=this.navParams.get('extraSecondLineForReinstatement');
  }

  
  goToHistory() {
    this.modalController.dismiss({ pageName: "serviceHistoryPage"});
  }

  closeModal() {
    this.modalController.dismiss({ pageName: "servicePage"});
  }

   /**
   * This method is used to dismiss the alert
   */
   dismiss() {
    this.modalController.dismiss();
  }
  
  ngOnInit() {
    console.log('ionViewDidLoad SuccessPopupPage');
  }

}
