import { Injectable } from "@angular/core";
import { LoggerServiceProvider } from "src/providers/logger-service/logger-service";
import { ConfigProvider } from "src/providers/utils/config";
import { UtilService } from "src/providers/utils/utils.service";

declare var WLResourceRequest;
/*
  Generated class for the getHealthInfoDetails .

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class HealthinfoDetailService {


 constructor( public logger: LoggerServiceProvider, public utils : UtilService) {
    this.logger.log("HealthinfoDetailService");
  }

   /** Get HealthInfo Details  **/
   getHealthInfoDetails(id) {
    this.logger.trace("HealthinfoDetailService--getHealthInfoDetails--start " );

    let promise = new Promise((resolve, reject) => {
      try {
        var resourceRequest =  new WLResourceRequest(ConfigProvider.getHealthInfoDetailsAdapterPath, WLResourceRequest.GET);
        resourceRequest.setQueryParameter('params',[id,this.utils.getToken()]);
        resourceRequest.send().then((response) => {
          resolve(response);
        },
        (error) => {
          this.logger.error("HealthinfoDetailService-- Adapter calling error:" + JSON.stringify(error));
          reject(error);
        });
      }catch (e) {
        this.logger.log("HealthinfoDetailService calling catch error:" + JSON.stringify(e));
        reject(e);
      }
    });
    this.logger.trace("HealthinfoDetailService--getHealthInfoDetails--end");
    return promise;
   }

};



