import { Injectable } from '@angular/core';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';

declare var WLResourceRequest;
/*
  Generated class for the SignUpServiceProvider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class SignUpServiceProvider {
  constructor(
    private utils: UtilService,
    private logger: LoggerServiceProvider,
    public config: ConfigProvider
  ) {

  }

  /** post registration check **/
  postRegistartionCheck(IdentityNumber, PolicyNumber) {
     this.logger.trace('SignUpService--Post Registration Check--start');

    let promise = new Promise((resolve, reject) => {
      try {
        if (this.utils.isDeviceOnLine()) {
          var resourceRequest = new WLResourceRequest(ConfigProvider.postRegistrationCheck,WLResourceRequest.POST);
          var data = {   params: "['" + IdentityNumber + "','" + PolicyNumber + "']",};
          resourceRequest.sendFormParameters(data).then(
            (response) => {
              resolve(response);
            },
            (error) => {
              this.logger.error(
                'SignUpService--Post Registration Check Adapter calling error:' +
                  JSON.stringify(error)
              );
              reject(error);
            }
          );
        } else {
          this.utils.dissmisProgressLoader();
          this.utils.showAlert(
            'Oops!!',
            'Connection Failed,Please check your connection and try aagain!'
          );
        }
      } catch (e) {
        reject(e);
        this.logger.log(
          'SignUpService--Post Registration Check calling catch error:' +
            JSON.stringify(e)
        );
      }
    });
    this.logger.trace('SignUpService--Post Registration Check--end');
    return promise;
  }

  /** post registration submit **/
  postRegistartionSubmit(
    IdentityNumber,
    PolicyNumber,
    Name,
    EmailAddress,
    Sourceofnews,
    SecurityQuestion,
    SecurityAnswer,
    SecurityWord,
    ReceiveENotification,
    TermsAndConditions
  ) {
    let promise = new Promise((resolve, reject) => {
      try {
        if (this.utils.isDeviceOnLine()) {
          var resourceRequest = new WLResourceRequest(ConfigProvider.postRegistrationSubmit, WLResourceRequest.POST);
          var data = {
            params:
              "['" +
              IdentityNumber +
              "','" +
              PolicyNumber +
              "', '" +
              Name +
              "', '" +
              EmailAddress +
              "',  '" +
              Sourceofnews +
              "', '" +
              SecurityQuestion +
              "', '" +
              SecurityAnswer +
              "',  '" +
              SecurityWord +
              "','" +
              ReceiveENotification +
              "', '" +
              TermsAndConditions +
              "']",
          };
          resourceRequest.sendFormParameters(data).then(
            (response) => {
              resolve(response);
            },
            (error) => {
              this.logger.error(
                'SignUpService--Post Registration Submit Adapter calling error:' +
                  JSON.stringify(error)
              );
              reject(error);
            }
          );
        }
      } catch (e) {
        reject(e);
        this.logger.log(
          'SignUpService--Post Registration Submit calling catch error:' +
            JSON.stringify(e)
        );
      }
    });
    // this.logger.trace('SignUpService--Post Registration Submit--end');
    return promise;
  }

  /** post ForgotPolicyNumber submit **/
  postForgotPolicyNumber(IdentityNumber, isRegisteredorNot, mobileOS) {
    this.logger.trace('ChangePasswordService--changePasswordService--start');
    let promise = new Promise((resolve, reject) => {
      try {
        var resourceRequest = new WLResourceRequest(
          ConfigProvider.getForgotPolicyNumberAdapterPath,
          WLResourceRequest.POST
        );
        var data = {
          params:
            "['" +
            IdentityNumber +
            "','" +
            isRegisteredorNot +
            "','" +
            mobileOS +
            "']",
        };
        resourceRequest.sendFormParameters(data).then(
          (response) => {
            resolve(response);
          },
          (error) => {
            this.logger.error(
              'ChangePasswordService--changePasswordService Adapter calling error:' +
                JSON.stringify(error)
            );
            reject(error);
          }
        );
      } catch (e) {
        reject(e);
        this.logger.log(
          'ChangePasswordService--changePasswordService adapter calling catch error:' +
            JSON.stringify(e)
        );
      }
    });
    this.logger.trace('ChangePasswordService--changePasswordService--end');
    return promise;
  }
}
